import HomePage from './pages/home.vue';
import MyDrinksPage from './pages/my-drinks.vue';
import Misc from './pages/misc.vue';
import AboutPage from './pages/about.vue';
import NotFoundPage from './pages/not-found.vue';

export default [
  {
    path: '/',
    component: HomePage,
  },
  {
    path: '/my-drinks/',
    component: MyDrinksPage,
  },
  {
    path: '/misc/',
    component: Misc,
  },
  {
    path: '/about/',
    component: AboutPage,
  },
  {
    path: '(.*)',
    component: NotFoundPage,
  },
];



