<template>
  <f7-toolbar tabbar :bottom-md="true" class="no-hairline">
    <f7-link class="text-color-white" v-on:click="goToPage()" tab-link-active>Drinks</f7-link>
    <f7-link class="text-color-white" v-on:click="goToPage('my-drinks')">My Drinks</f7-link>
    <f7-link class="text-color-white" v-on:click="goToPage('misc')">Misc</f7-link>
  </f7-toolbar>
</template>
<script>
  export default {
    name: 'toolbarComponent',
    methods: {
      goToPage(page) {
        page = page ? `/${page}/` : '/';
        this.$f7.views.current.router.navigate(page);
      }
    }
  }
</script>



