<template>
  <f7-swiper class="alphabet-swiper" navigation :navigation="false" :params="{speed:500, slidesPerView: 4, spaceBetween: 20}">
    <f7-swiper-slide v-for="(letter, index) in alphabet" :key="index">
      <f7-button v-on:click="changePagination(letter)">{{ letter }}</f7-button>
    </f7-swiper-slide>
  </f7-swiper>
</template>
<script>
  import { main } from '../service/main'

  export default {
    name: 'paginationComponent',
    props: ['drink', 'callback'],
    data () {
      return {
        alphabet: main.getAlphabet(),
      }
    },
    mounted () {
    },
    methods: {
      changePagination(letter) {
        if (typeof this.callback === 'function') {
          this.callback(letter);
        }
      },
    }
  }
</script>



