<template>
  <f7-page v-on:page:afterin="afterIn">
    <f7-navbar title="Measurements"></f7-navbar>

    <toolbarComponent></toolbarComponent>

    <f7-row no-gap>
      <f7-col width="0" tablet-width="20" desktop-width="35"></f7-col>
      <f7-col width="100" tablet-width="60" desktop-width="30">

        <f7-card class="drink-card" v-if="measurements" v-for="(measurement, index) in measurements" :key="index">
          <f7-card-content>
            <table class="bar-table">
              <thead>
              <tr>
                <th colspan="2">{{ measurement.title }}</th>
              </tr>
              </thead>
              <tbody>
              <tr v-for="(data, i) in measurement.data" :key="i">
                <td>{{ data[0] }}</td>
                <td>{{ data[1] }}</td>
              </tr>
              </tbody>
            </table>
          </f7-card-content>
        </f7-card>

        <f7-card class="drink-card">
          <f7-card-content>
            <p><strong>*</strong> A <i>"shotglass"</i> is usually 1.5 ounces, but sometimes 2 ounces with a measuring line at 1.5 ounces.
              <br><br>You can also buy (in US) <i>"short shot"</i> glasses or <i>"pony shots"</i> which are 1 ounce.
              <br><br>Pony shots are usually used with martinis, manhattans, and rob roy's.</p>
          </f7-card-content>
        </f7-card>

      </f7-col>
      <f7-col width="0" tablet-width="20" desktop-width="35"></f7-col>
    </f7-row>

  </f7-page>
</template>

<script>
  import {main} from '../service/main';
  import toolbarComponent from '../components/toolbarComponent';

  export default {
    components: {
      toolbarComponent
    },
    data() {
      return {
        measurements: null
      }
    },
    mounted() {
      this.$f7ready(() => {
        this.measurements = main.getMeasurements();
        this.$f7.preloader.hide();
        console.log(this.measurements);
      });
    },
    methods: {
      afterIn() {

      }
    }
  }
</script>



