<template>
  <f7-page v-on:page:afterin="afterIn">
    <f7-navbar title="My bar">
      <f7-nav-right>
        <f7-link class="searchbar-enable" data-searchbar=".card-searchbar" icon-ios="f7:search_strong" icon-md="material:search"></f7-link>
      </f7-nav-right>
      <f7-searchbar class="card-searchbar" expandable v-on:input="searchDrinks"></f7-searchbar>
    </f7-navbar>

    <toolbarComponent></toolbarComponent>

    <f7-row no-gap v-if="drinks">
      <f7-col width="0" tablet-width="10" desktop-width="35"></f7-col>
      <f7-col width="100" tablet-width="80" desktop-width="30">

        <div class="text-color-white search-help">Search by drink name or ingredients
          <f7-icon f7="up"></f7-icon>
        </div>

        <filterComponent :callback="changeFilter"></filterComponent>

        <paginationComponent :callback="changePagination"></paginationComponent>

        <cardComponent v-for="drink in drinks" :key="drink.key" v-if="favoriteDrinks.indexOf(drink.key) === -1" :drink="drink"></cardComponent>

      </f7-col>
      <f7-col width="0" tablet-width="10" desktop-width="35"></f7-col>
    </f7-row>

  </f7-page>
</template>
<script>
  import { main } from '../service/main'
  import toolbarComponent from '../components/toolbarComponent'
  import cardComponent from '../components/cardComponent'
  import filterComponent from '../components/filterComponent'
  import paginationComponent from '../components/paginationComponent'

  export default {
    components: {
      toolbarComponent,
      cardComponent,
      filterComponent,
      paginationComponent,
    },
    data () {
      return {
        drinks: null,
        levels: null,
        page: 'main',
        favoriteDrinks: JSON.parse(localStorage.getItem('favorite-drinks')) || [],
      }
    },
    mounted () {
      this.$f7.preloader.show()
      this.$f7ready(() => {
      })
    },
    methods: {
      afterIn () {
        this.drinks = main.getDrinks()
        this.$f7.preloader.hide()

        console.warn(this.drinks)
      },

      searchDrinks (event) {
        let drinks = main.getAllDrinks()

        this.drinks = drinks.filter(obj => {
          let string = JSON.stringify(obj).toLowerCase()
          let array = event.target.value.trim().toLowerCase().split(' ')

          return new RegExp(array.join('|')).test(string)
        })
      },

      changeFilter (data) {
        this.drinks = main.filterDrinks(data, this.page)
      },

      changePagination (page) {
        this.page = page
        this.drinks = main.getDrinks(page)
      }
    }
  }
</script>



