<template>
  <f7-page v-on:page:afterin="afterIn">
    <f7-navbar title="My Drinks" back-link="Back"></f7-navbar>

    <toolbarComponent></toolbarComponent>

    <f7-row no-gap>
      <f7-col width="0" tablet-width="20" desktop-width="35"></f7-col>
      <f7-col width="100" tablet-width="60" desktop-width="30">
        <cardComponent v-for="drink in drinks" :key="drink.key" :drink="drink" :favoriteDrink="true"></cardComponent>
      </f7-col>
      <f7-col width="0" tablet-width="20" desktop-width="35"></f7-col>
    </f7-row>
  </f7-page>
</template>

<script>
  import { main } from '../service/main'
  import toolbarComponent from '../components/toolbarComponent'
  import cardComponent from '../components/cardComponent'

  export default {
    components: {
      toolbarComponent,
      cardComponent
    },
    data () {
      return {
        drinks: null,
        favoriteDrinks: JSON.parse(localStorage.getItem('favorite-drinks')) || [],
      }
    },
    mounted () {
      this.$f7.preloader.show()
      this.$f7ready(() => {
      })
    },
    methods: {
      afterIn () {
        this.getMyDrinks()
        this.$f7.preloader.hide()
      },
      getMyDrinks () {
        if (this.favoriteDrinks.length) {
          const drinks = main.getDrinks()
          let myDrinks = []
          for (let i = 0; i < this.favoriteDrinks.length; i++) {
            myDrinks.push(drinks[this.favoriteDrinks[i]])
          }

          this.drinks = myDrinks
        }
      }
    }
  }
</script>



