<template>
  <div>
    <f7-card class="card-filter" v-if="filterData">
      <f7-row>
        <f7-col width="40">
          <f7-list class="filter-list no-margin" inset>
            <f7-list-item>
              <f7-label>Ingredient count</f7-label>
              <f7-input type="select" @input="form.ingredient = $event.target.value" :value="form.ingredient" v-on:change="changeFilter">
                <option value="" selected>Any</option>
                <option v-for="(count, index) in filterData.ingredientCount" :key="index" :value="count">{{ count }}</option>
              </f7-input>
            </f7-list-item>
          </f7-list>
        </f7-col>
        <f7-col width="60">
          <f7-list class="filter-list no-margin" inset>
            <f7-list-item>
              <f7-label>Drink type</f7-label>
              <f7-input type="select" @input="form.category = $event.target.value" :value="form.category" v-on:change="changeFilter">
                <option value="" selected>Any</option>
                <option v-for="(category, index) in filterData.categories" :key="index" :value="category">{{ category }}</option>
              </f7-input>
            </f7-list-item>
          </f7-list>
        </f7-col>
      </f7-row>
      <f7-row>
        <f7-col>
          <f7-list class="filter-list no-margin" inset>
            <f7-list-item>
              <f7-label>Glass type</f7-label>
              <f7-input type="select" @input="form.glass = $event.target.value" :value="form.glass" v-on:change="changeFilter">
                <option value="" selected>Any</option>
                <option v-for="(glass, index) in filterData.glassTypes" :key="index" :value="glass">{{ glass }}</option>
              </f7-input>
            </f7-list-item>
          </f7-list>
        </f7-col>
      </f7-row>
    </f7-card>
  </div>
</template>

<script>
  import {main} from '../service/main';

  export default {
    name: 'filterComponent',
    props: ['callback'],
    data() {
      return {
        filterData: main.getDrinkFilter(),
        form: {ingredient: '', category: '', glass: '', myBar: false},
      }
    },
    mounted() {

    },
    computed: {},
    methods: {
      changeFilter() {
        if (typeof this.callback === 'function') {
          this.callback(this.form);
        }
      },
    }
  }
</script>



