<template>
  <f7-card class="drink-card" v-if="drink">
    <f7-card-header>
      <span>{{ drink.category }} / {{ drink.ingredients.length }} ingredients</span>
      <f7-link class="bookmark" :icon-f7="isFavoriteDrink ? 'bookmark_fill' : 'bookmark'" v-on:click="setFavorite"></f7-link>
    </f7-card-header>
    <f7-card-content>
      <f7-list>
        <f7-list-item link="#" @click="createPopup">
          <h3>{{ drink.name }}</h3>
        </f7-list-item>
      </f7-list>
    </f7-card-content>
  </f7-card>
</template>
<script>
  import { main } from '../service/main'

  export default {
    name: 'cardComponent',
    props: ['drink', 'favoriteDrink'],
    data () {
      return {
        hide: false,
        isFavorite: this.favoriteDrink,
      }
    },
    mounted () {
    },
    computed: {
      isFavoriteDrink () {
        return this.isFavorite
      }
    },
    methods: {
      setFavorite () {
        this.isFavorite = main.setFavorite(this.drink.key)
      },
      createPopup () {
        this.$f7.popup.create({ content: this.getPopupTemplate().trim() }).open()
      },

      getPopupTemplate () {
        return `
          <div class="popup">
            <div class="page">
              <div class="navbar" style="background: rgb(41, 146, 203)">
                <div class="navbar-inner">
                  <div class="title">${this.drink.name}</div>
                  <div class="right"><a href="#" class="link popup-close">Close</a></div>
                </div>
              </div>
              <div class="page-content">
                <div class="block">
                  <p><small>${this.drink.category} / ${this.drink.ingredients.length} ingredients</small></p>

                  <div class="position-relative">
                    <ul class="no-margin-bottom">${this.getIngredientsTemplate(this.drink.ingredients)}</ul>
                  </div>

                  <div>
                    <p><strong>Directions</strong></p>
                    <p>${this.drink.directions}</p>
                  </div>

                  <p>${this.drink.serve ? '<strong>Serve in</strong> ' + this.drink.serve : ''}</p>
                </div>
              </div>
            </div>
          </div>
        `
      },

      getIngredientsTemplate (ingredients) {
        let lis = ''
        for (let i = 0; i < ingredients.length; i++) {
          lis += `<li>${ingredients[i]}</li>`
        }

        return lis
      },
    }
  }
</script>



