import { drinks } from '../service/drinks'

export class Main {

  /**
   * @param data
   * @returns {boolean}
   */
  isNull (data) {
    if (data === null) return true
    if (data === 'null') return true
    if (data === undefined) return true
    if (data === 'undefined') return true

    return false
  }

  /**
   * @param index
   * @returns {boolean}
   */
  setFavorite (index) {
    let favoriteDrinks = localStorage.getItem('favorite-drinks')

    if (!favoriteDrinks) {
      localStorage.setItem('favorite-drinks', JSON.stringify([index]))
      return true
    }

    favoriteDrinks = JSON.parse(favoriteDrinks)
    const favoriteIndex = favoriteDrinks.indexOf(index)

    if (favoriteIndex !== -1) {
      favoriteDrinks.splice(favoriteIndex, 1)
    } else {
      favoriteDrinks.push(index)
    }

    localStorage.setItem('favorite-drinks', JSON.stringify(favoriteDrinks))

    return favoriteDrinks.indexOf(index) !== -1
  }

  /**
   * @returns {{ingredientCount: this, categories: this, glassTypes: this}}
   */
  getDrinkFilter () {
    return {
      ingredientCount: 10,
      categories: [
        'Cocktail',
        'Cocoa', 'Coffee / Tea',
        'Drink',
        'Liqueur / Cordial',
        'Milk',
        'Punch / Party Drink',
        'Shot',
        'Soft Drink / Soda'
      ],
      glassTypes: [
        'Beer mug',
        'Beer pilsner',
        'Brandy snifter',
        'Champagne flute',
        'Cocktail glass',
        'Coffee mug',
        'Collins glass',
        'Highball glass',
        'Hurricane glass',
        'Irish coffee cup',
        'Margarita glass',
        'Mason jar',
        'Old-fashioned glass',
        'Pitcher',
        'Pousse cafe glass',
        'Punch bowl',
        'Sherry glass',
        'Shot glass',
        'Whisky sour glass',
        'White wine glass',
        'Whisky sour glass'
      ],
    }
  }

  /**
   * @param data
   * @param page
   * @returns {*[]}
   */
  filterDrinks (data, page = 'main') {
    let drinks = this.getDrinks(page)

    if (data.ingredient !== '') drinks = drinks.filter(obj => parseInt(data.ingredient) === obj.ingredients.length)
    if (data.category !== '') drinks = drinks.filter(obj => data.category === obj.category)
    if (data.glass !== '') drinks = drinks.filter(obj => obj.serve && data.glass === obj.serve)

    return drinks
  }

  /**
   * @returns {string[]}
   */
  getAlphabet () {
    return 'main,a,b,c,d,e,f,g,h,i,j,k,l,m,n,o,p,q,r,s,t,u,v,w,x,y,z'.split(',')
  }

  getDrinksAlphabet (drinks) {
    let main = []
    let a = []
    let b = []
    let c = []
    let d = []
    let e = []
    let f = []
    let g = []
    let h = []
    let i = []
    let j = []
    let k = []
    let l = []
    let m = []
    let n = []
    let o = []
    let p = []
    let q = []
    let r = []
    let s = []
    let t = []
    let u = []
    let v = []
    let w = []
    let x = []
    let y = []
    let z = []

    for (let ii = 0; ii < drinks.length; ii++) {
      let name = drinks[ii].name.toLowerCase()

      if (name.startsWith('a')) a.push(drinks[ii])
      else if (name.startsWith('b')) b.push(drinks[ii])
      else if (name.startsWith('c')) c.push(drinks[ii])
      else if (name.startsWith('d')) d.push(drinks[ii])
      else if (name.startsWith('e')) e.push(drinks[ii])
      else if (name.startsWith('f')) f.push(drinks[ii])
      else if (name.startsWith('g')) g.push(drinks[ii])
      else if (name.startsWith('h')) h.push(drinks[ii])
      else if (name.startsWith('i')) i.push(drinks[ii])
      else if (name.startsWith('j')) j.push(drinks[ii])
      else if (name.startsWith('k')) k.push(drinks[ii])
      else if (name.startsWith('l')) l.push(drinks[ii])
      else if (name.startsWith('m')) m.push(drinks[ii])
      else if (name.startsWith('n')) n.push(drinks[ii])
      else if (name.startsWith('o')) o.push(drinks[ii])
      else if (name.startsWith('p')) p.push(drinks[ii])
      else if (name.startsWith('q')) q.push(drinks[ii])
      else if (name.startsWith('r')) r.push(drinks[ii])
      else if (name.startsWith('s')) s.push(drinks[ii])
      else if (name.startsWith('t')) t.push(drinks[ii])
      else if (name.startsWith('u')) u.push(drinks[ii])
      else if (name.startsWith('v')) v.push(drinks[ii])
      else if (name.startsWith('w')) w.push(drinks[ii])
      else if (name.startsWith('x')) x.push(drinks[ii])
      else if (name.startsWith('y')) y.push(drinks[ii])
      else if (name.startsWith('z')) z.push(drinks[ii])
      else main.push(drinks[ii])
    }

    return {
      main: main, a: a, b: b, c: c, d: d, e: e, f: f, g: g, h: h, i: i, j: j, k: k, l: l, m: m, n: n, o: o, p: p, q: q, r: r, s: s, t: t, u: u, v: v, w: w, x: x, y: y, z: z
    }
  }

  /**
   * @returns {*}
   */
  getMeasurements () {
    return drinks.getMeasurements()
  }

  /**
   * @param type
   * @returns {*}
   */
  getDrinks (type = 'main') {
    const drinksData = drinks.getDrinks()

    return this.getDrinksAlphabet(drinksData)[type]
  }

  /**
   * @returns {*}
   */
  getAllDrinks () {
    return drinks.getDrinks()
  }

  /**
   * @returns {*[]}
   */
  getIngredients () {
    return drinks.getIngredients()
  }
}

export const
  main = new Main()



